import type { FC } from 'react';
import { Modal } from 'antd';
import MarkdownRender from '../MarkdownRender/MarkdownRender.tsx';
import { markdown } from './privacy.md';

type PrivacyModalProps = {
  isOpen: boolean;
  onClose: () => void;
};

const PrivacyModal: FC<PrivacyModalProps> = ({ isOpen, onClose }) => {
  return (
    <Modal
      title="Privacy policy"
      open={isOpen}
      onCancel={onClose}
      onOk={onClose}
      okText="Accept"
      width={800}
      styles={{
        body: {
          padding: 0,
          maxHeight: '70vh',
          overflowY: 'auto',
        },
      }}
    >
      <MarkdownRender markdown={markdown} />
    </Modal>
  );
};

export default PrivacyModal;
